import logo from './logo.svg';
import { Navbar,Container,Nav,NavDropdown } from 'react-bootstrap';
import './App.css';
import { useState } from 'react';
import shoesData from './data.js'
import Detail from './Detail.js'
import axios from 'axios';

import { Link, Route, Switch, Routes }from 'react-router-dom'

function App() {


  let [shoes, changeShoes] = useState(shoesData);
  let [stocks, setStocks] = useState([10,11,12]);

  return (
    <div className="App">
      <Navbar bg="light" expand="lg">
        <Container>
          <Navbar.Brand href="#home">Koni-Shop</Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="me-auto">
              <Nav.Link as={Link} to='/'>Home</Nav.Link>
              <Nav.Link as={Link} to='/detail'>Detail</Nav.Link>
              <NavDropdown title="Dropdown" id="basic-nav-dropdown">
                <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
                <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
                <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
                <NavDropdown.Divider />
                <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
              </NavDropdown>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>

      

        <Route exact path="/">
          <div className='Jumbotron'>
            <h1>Hello </h1>
            <p>
              This is koni's shop
            </p>
          </div>

          <div className='container'>
            <div className='row'>

              <Card shoes={shoes}/>

            </div>

            <button className='btn btn-primary' onClick={()=>{ 

              // ui 

              //axios  JSON Object  .
              axios.get('https://codingapple1.github.io/shop/data2.json')
                  .then((d)=>{ 

                    //  ui  .
                    console.log(d.data);
                    changeShoes([...shoes, ...d.data]);
                   })// then
                  .catch()// catch

              }}></button>
          </div>
        </Route>

        <Route path="/detail/:id">//   :id
          <Detail shoes={shoes} stocks={stocks} setStocks={setStocks}></Detail>
        </Route>
      
    </div>
  );
}



function Card(props){
  

  return (
    
      props.shoes.map((shoe,i) => {
          return (

            <div className='col-md-4' key={i} path={`/detail/${i}`}>
              <img src={ `https://codingapple1.github.io/shop/shoes${i+1}.jpg` } width="100%"/>
              <h4>{ shoe.title }</h4>
              <p>{ shoe.content }</p>
              <p>{ shoe.price }</p>
            </div>

          )

      })

    
  )
}

export default App;
